"use client";
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import {
  Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper,
  Button, TextField, Dialog, DialogActions, DialogContent, DialogTitle,
  TablePagination, TableSortLabel, Toolbar, InputAdornment
} from '@mui/material';
import { Search } from '@mui/icons-material';

interface Product {
  id?: number;
  nombre: string;
  valor_comercial: number;
  valor_unitario: number;
  lista_1: number;
  lista_2: number;
  lista_3: number;
  cantidad: number;
}

const ProductTable: React.FC = () => {
  const [products, setProducts] = useState<Product[]>([]);
  const [form, setForm] = useState<Product>({ nombre: '', valor_comercial: 0, valor_unitario: 0, lista_1: 0, lista_2: 0, lista_3: 0, cantidad: 0 });
  const [isEditing, setIsEditing] = useState<boolean>(false);
  const [open, setOpen] = useState<boolean>(false);
  const [page, setPage] = useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = useState<number>(5);
  const [order, setOrder] = useState<'asc' | 'desc'>('asc');
  const [orderBy, setOrderBy] = useState<keyof Product>('nombre');
  const [search, setSearch] = useState<string>('');

  useEffect(() => {
    fetchProducts();
  }, []);

  const fetchProducts = async () => {
    const response = await axios.get('http://localhost:8000/products');
    setProducts(response.data);
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (isEditing) {
      await axios.put(`http://localhost:8000/products/${form.id}`, form);
    } else {
      await axios.post('http://localhost:8000/products', form);
    }
    setForm({ nombre: '', valor_comercial: 0, valor_unitario: 0, lista_1: 0, lista_2: 0, lista_3: 0, cantidad: 0 });
    setIsEditing(false);
    setOpen(false);
    fetchProducts();
  };

  const handleEdit = (product: Product) => {
    setForm(product);
    setIsEditing(true);
    setOpen(true);
  };

  const handleDelete = async (id: number) => {
    await axios.delete(`http://localhost:8000/products/${id}`);
    fetchProducts();
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setForm({ nombre: '', valor_comercial: 0, valor_unitario: 0, lista_1: 0, lista_2: 0, lista_3: 0, cantidad: 0 });
    setIsEditing(false);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleRequestSort = (property: keyof Product) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearch(event.target.value);
  };

  const filteredProducts = products.filter((product) =>
    product.nombre.toLowerCase().includes(search.toLowerCase())
  );

  const sortedProducts = filteredProducts.sort((a, b) => {
    if (orderBy === 'valor_comercial' || orderBy === 'valor_unitario' || orderBy === 'cantidad' || orderBy === 'lista_1' || orderBy === 'lista_2' || orderBy === 'lista_3') {
      return order === 'asc' ? a[orderBy] - b[orderBy] : b[orderBy] - a[orderBy];
    } else {
      return order === 'asc'
        ? (a[orderBy] as string).localeCompare(b[orderBy] as string)
        : (b[orderBy] as string).localeCompare(a[orderBy] as string);
    }
  });

  const paginatedProducts = sortedProducts.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

  return (
    <div className="container mx-auto p-4 min-h-screen bg-gray-100">
      <h1 className="text-3xl font-bold text-amber-400 mb-6 text-center">INVENTARIO FERREMOLINA</h1>
      <Toolbar className="flex justify-between mb-4">
        <Button variant="contained" color="primary" onClick={handleClickOpen}>
          Add Product
        </Button>
        <TextField
          variant="outlined"
          placeholder="Search..."
          value={search}
          onChange={handleSearch}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <Search />
              </InputAdornment>
            ),
          }}
        />
      </Toolbar>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>{isEditing ? 'Edit Product' : 'Add Product'}</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            name="nombre"
            label="Nombre"
            type="text"
            fullWidth
            value={form.nombre}
            onChange={handleChange}
          />
          <TextField
            margin="dense"
            name="valor_comercial"
            label="Valor Comercial"
            type="number"
            fullWidth
            value={form.valor_comercial}
            onChange={handleChange}
          />
          <TextField
            margin="dense"
            name="valor_unitario"
            label="Valor Unitario"
            type="number"
            fullWidth
            value={form.valor_unitario}
            onChange={handleChange}
          />
          <TextField
            margin="dense"
            name="lista_1"
            label="Lista 1"
            type="number"
            fullWidth
            value={form.lista_1}
            onChange={handleChange}
          />
          <TextField
            margin="dense"
            name="lista_2"
            label="Lista 2"
            type="number"
            fullWidth
            value={form.lista_2}
            onChange={handleChange}
          />
          <TextField
            margin="dense"
            name="lista_3"
            label="Lista 3"
            type="number"
            fullWidth
            value={form.lista_3}
            onChange={handleChange}
          />
          <TextField
            margin="dense"
            name="cantidad"
            label="Cantidad"
            type="number"
            fullWidth
            value={form.cantidad}
            onChange={handleChange}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleSubmit} color="primary">
            {isEditing ? 'Update' : 'Add'}
          </Button>
        </DialogActions>
      </Dialog>
      <TableContainer component={Paper} className="shadow-lg">
        <Table>
          <TableHead className="bg-gray-200">
            <TableRow>
              <TableCell sortDirection={orderBy === 'id' ? order : false}>
                <TableSortLabel
                  active={orderBy === 'id'}
                  direction={orderBy === 'id' ? order : 'asc'}
                  onClick={() => handleRequestSort('id')}
                >
                  ID
                </TableSortLabel>
              </TableCell>
              <TableCell sortDirection={orderBy === 'nombre' ? order : false}>
                <TableSortLabel
                  active={orderBy === 'nombre'}
                  direction={orderBy === 'nombre' ? order : 'asc'}
                  onClick={() => handleRequestSort('nombre')}
                >
                  Nombre
                </TableSortLabel>
              </TableCell>
              <TableCell sortDirection={orderBy === 'valor_comercial' ? order : false}>
                <TableSortLabel
                  active={orderBy === 'valor_comercial'}
                  direction={orderBy === 'valor_comercial' ? order : 'asc'}
                  onClick={() => handleRequestSort('valor_comercial')}
                >
                  Valor Comercial
                </TableSortLabel>
              </TableCell>
              <TableCell sortDirection={orderBy === 'valor_unitario' ? order : false}>
                <TableSortLabel
                  active={orderBy === 'valor_unitario'}
                  direction={orderBy === 'valor_unitario' ? order : 'asc'}
                  onClick={() => handleRequestSort('valor_unitario')}
                >
                  Valor Unitario
                </TableSortLabel>
              </TableCell>
              <TableCell sortDirection={orderBy === 'lista_1' ? order : false}>
                <TableSortLabel
                  active={orderBy === 'lista_1'}
                  direction={orderBy === 'lista_1' ? order : 'asc'}
                  onClick={() => handleRequestSort('lista_1')}
                >
                  Lista 1
                </TableSortLabel>
              </TableCell>
              <TableCell sortDirection={orderBy === 'lista_2' ? order : false}>
                <TableSortLabel
                  active={orderBy === 'lista_2'}
                  direction={orderBy === 'lista_2' ? order : 'asc'}
                  onClick={() => handleRequestSort('lista_2')}
                >
                  Lista 2
                </TableSortLabel>
              </TableCell>
              <TableCell sortDirection={orderBy === 'lista_3' ? order : false}>
                <TableSortLabel
                  active={orderBy === 'lista_3'}
                  direction={orderBy === 'lista_3' ? order : 'asc'}
                  onClick={() => handleRequestSort('lista_3')}
                >
                  Lista 3
                </TableSortLabel>
              </TableCell>
              <TableCell sortDirection={orderBy === 'cantidad' ? order : false}>
                <TableSortLabel
                  active={orderBy === 'cantidad'}
                  direction={orderBy === 'cantidad' ? order : 'asc'}
                  onClick={() => handleRequestSort('cantidad')}
                >
                  Cantidad
                </TableSortLabel>
              </TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedProducts.map((product) => (
              <TableRow key={product.id}>
                <TableCell>{product.id}</TableCell>
                <TableCell>{product.nombre}</TableCell>
                <TableCell>{product.valor_comercial}</TableCell>
                <TableCell>{product.valor_unitario}</TableCell>
                <TableCell>{product.lista_1}</TableCell>
                <TableCell>{product.lista_2}</TableCell>
                <TableCell>{product.lista_3}</TableCell>
                <TableCell>{product.cantidad}</TableCell>
                <TableCell>
                  <div className="flex space-x-4">
                    <div
                      onClick={() => handleEdit(product)}
                      className="bg-blue-500 hover:bg-blue-400 text-white font-bold py-2 px-4 border-b-4 border-blue-700 hover:border-blue-500 rounded"
                    >
                      Edit
                    </div>
                    <div
                      className="bg-red-500 hover:bg-red-400 text-white font-bold py-2 px-4 border-b-4 border-red-700 hover:border-red-500 rounded"
                      onClick={() => handleDelete(product.id!)}
                    >
                      Delete
                    </div>
                  </div>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25, 100, 1000]}
        component="div"
        count={sortedProducts.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </div>
  );
};

export default ProductTable;